body {
  background: #f8f8f8;
}
header {
  width: 100%;
  height: 0.9rem;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  padding: 0.26rem 0.32rem;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  background: #78c652;
  clear: both;
}
header:after {
  content: "";
  display: block;
  clear: both;
}
header .hlogo {
  float: left;
  width: 1.90rem;
}
header .hlogo img {
  display: inline-block;
  width: 0.72rem;
  height: auto;
}
header .haddress {
  float: left;
  width: 2.25rem;
}
header .haddress a {
  color: white;
  font-size: 0.27rem;
}
header .haddress a span {
  margin: 0 0.11rem;
}
header .haddress a i {
  font-size: 0.27rem;
}
header .hsearch {
  font-size: 0.36rem;
  float: right;
  color: white;
}
.main {
  padding-top: 0.9rem;
  padding-bottom: 0.98rem;
}
.main .homeContainer .ul1 {
  width: 100%;
  clear: both;
}
.main .homeContainer .ul1:after {
  content: "";
  display: block;
  clear: both;
}
.main .homeContainer .ul1 li {
  float: left;
  width: 25%;
  text-align: center;
}
.main .homeContainer .ul1 li a {
  display: inline-block;
  width: 100%;
  height: 100%;
  background: white;
}
.main .homeContainer .ul1 li a img {
  width: 100%;
  height: auto;
}
.main .homeContainer .click {
  margin-top: 0.04rem;
  padding: 0.15rem 0.24rem;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  clear: both;
  background: white;
}
.main .homeContainer .click:after {
  content: "";
  display: block;
  clear: both;
}
.main .homeContainer .click a {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.main .homeContainer .click h6 {
  float: left;
  font-size: 0.28rem;
  color: #78c652;
  font-weight: normal;
  padding-right: 0.23rem;
  border-right: 0.02rem solid #e4e4e4;
}
.main .homeContainer .click p {
  float: left;
  color: #fd983c;
  font-size: 0.20rem;
}
.main .homeContainer .click p i {
  color: #ff8f30;
  font-size: 0.27rem;
  margin-left: 0.23rem;
  margin-right: 0.07rem;
}
.main .homeContainer .ul2 li {
  margin-top: 0.08rem;
  width: 100%;
}
.main .homeContainer .ul2 li a {
  display: inline-block;
  width: 100%;
  height: 2.50rem;
}
.main .homeContainer .ul2 li a img {
  width: 100%;
  height: auto;
}
.main .homeContainer .ul2 li a.special {
  display: inline-block;
  width: 100%;
  height: 1.11rem;
}
footer {
  position: fixed;
  z-index: 10;
  background: #f7f7f7;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 0.01rem solid #a7a795;
}
footer ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
footer ul li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-top: 0.04rem;
}
footer ul li.active a {
  color: #78c652;
}
footer ul li a {
  display: block;
  height: 0.98rem;
  text-align: center;
  font-size: 0.18rem;
  color: #fff;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  color: #929292;
}
footer ul li a .iconfont {
  height: 0.58rem;
  line-height: 0.58rem;
  font-size: 0.45rem;
}
.list_header {
  padding: 0.14rem 0.16rem;
  position: fixed;
  top: 0;
  left: 0;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  width: 100%;
  z-index: 9;
  height: 0.9rem;
  box-sizing: border-box;
  background: white;
  border-bottom: 0.01rem solid #aaabac;
}
.list_header input {
  background: #f4f5f7;
  width: 100%;
  height: 100%;
  border-radius: 0.3rem;
  color: #bebfc7;
  border: 0 none;
  font-size: 0.23rem;
  position: relative;
  text-indent: 0.6rem;
}
.list_header i {
  color: #717171;
  font-size: 0.36rem;
  position: absolute;
  top: 0.3rem;
  left: 0.3rem;
}
.list_main {
  width: 100%;
  height: 100%;
  position: relative;
  background: #f4f5f7;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.list_main nav {
  width: 1.37rem;
  height: 100%;
  position: fixed;
  z-index: 2;
  overflow-y: auto;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  top: 0.9rem;
  left: 0;
  background: #f4f5f7;
  padding-bottom: 0.98rem;
  float: left;
}
.list_main nav ul {
  width: 100%;
  padding-bottom: 0.98rem;
}
.list_main nav ul li {
  font-size: 0.27rem;
  background: white;
  padding: 0.3rem 0;
  text-align: center;
  border-bottom: 0.02rem solid #e6e6e6;
}
.list_main nav ul li.active {
  background: #f4f5f7;
}
.list_main nav ul li.active p {
  border-left: 0.1rem solid #f96d16;
  color: #f96d16;
}
.list_main section {
  padding: 0.11rem 0.20rem;
  box-sizing: border-box;
  background: #f4f5f7;
  overflow-y: auto;
  position: absolute ! important;
  top: 0.9rem;
  right: 0;
  padding-bottom: 0.98rem;
  width: 5rem;
}
.list_main section ul {
  height: auto;
  background: white;
  padding: 0.14rem 0.12rem;
  box-sizing: border-box;
}
.list_main section ul li a {
  display: inline-block;
  width: 33.33%;
  float: left;
}
.list_main section ul li a p {
  font-size: 0.26rem;
  color: #666;
  text-align: center;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.list_main section ul li a img {
  width: 100%;
  height: auto;
}
.listDetail_Header {
  width: 100%;
  height: 0.86rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background: white;
  border-bottom: 0.01rem solid #b2b2b2;
}
.listDetail_Header .i_left {
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  font-size: 0.35rem;
  color: #808080;
}
.listDetail_Header p {
  font-size: 0.29rem;
  text-align: center;
  height: 0.86rem;
  line-height: 0.86rem;
  color: #333;
}
.listDetail_Header .i_right {
  position: absolute;
  top: 0.25rem;
  right: 0.25rem;
  font-size: 0.44rem;
  color: #808080;
}
.listDetail_Header span {
  display: inline-block;
  width: 0.32rem;
  height: 0.32rem;
  border-radius: 50%;
  background: #f00;
  color: white;
  line-height: 0.32rem;
  text-align: center;
  font-size: 0.16rem;
  position: absolute;
  top: -0.1rem;
  right: -0.1rem;
  z-index: 3;
}
.listDetail_main {
  padding-top: 0.86rem;
  width: 100%;
}
.listDetail_main ul {
  width: 100%;
  background: white;
}
.listDetail_main ul li {
  padding: 0 0.21rem;
}
.listDetail_main ul li a {
  display: inline-block;
  width: 100%;
  height: auto;
  border-bottom: 0.02rem solid #e6e6e6;
}
.listDetail_main ul li a .img-box {
  width: 1.77rem;
  height: 1.77rem;
  float: left;
}
.listDetail_main ul li a .img-box img {
  width: 100%;
  height: auto;
}
.listDetail_main ul li a .content-box {
  overflow: hidden;
  height: 1.77rem;
  padding-left: 0.2rem;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  position: relative ! important;
}
.listDetail_main ul li a .content-box h3 {
  margin-top: 0.11rem;
  width: 100%;
  font-size: 0.28rem;
  font-weight: normal;
  color: #333;
  white-space: normal;
}
.listDetail_main ul li a .content-box p {
  width: 100%;
  position: absolute ! important;
  left: 0.2rem;
  bottom: 0.2rem;
}
.listDetail_main ul li a .content-box p span {
  color: #f96d16;
  font-size: 0.22rem;
  margin-right: 0.13rem;
}
.listDetail_main ul li a .content-box p del {
  color: #999;
  font-size: 0.16rem;
}
.listDetail_main ul li a .content-box p i {
  position: absolute ! important;
  right: 0.2rem;
  bottom: 0;
  z-index: 5;
  display: inline-block;
  font-size: 0.5rem;
  color: #f2c0a4;
}
